* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: rgb(63, 63, 63);
  font-family: "Poppins", sans-serif;
  height: auto;
}
.Main_Cntnr {
  background-color: white;
  height: 100vh;
  width: 100%;
  display: flex;
  position: relative;
}
.logobox_Jb_dtl {
  width: 22%;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  background-color: whitesmoke;
  flex-direction: column;
  box-shadow: 0 0 4px;
}
.logo {
  margin: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  background-color: #fff;
}
.Jb_dtl_list {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(63, 63, 63);
  margin: 20% 0 0 5%;
  flex-direction: column;
  margin-left: 25px;
}
.Jb_dtl_list li {
  padding-bottom: 15px;
  font-size: 20px;
}

.leftbtns_Jb_dtl {
  margin-top: 50%;
}
.Emailbtn_Jb_dtl {
  width: 140px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #008bdf;
  color: #fff;
  border: none;
  margin: 15px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}

.Supportbtn_Jb_dtl {
  width: 140px;
  cursor: pointer;
  background-color: #c4dceb;
  color: #192a3e;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 15px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}
.lftmail .fa-envelope,
.lftphone .fa-phone-alt {
  padding-right: 8px;
  color: #192a3e;
}
.lftmail a {
  color: #008bdf;

  text-decoration: none;
}
.lftmail,
.lftphone {
  display: flex;
  align-items: center;
  justify-content: center;
}
.lftphone {
  color: #192a3e;
  text-align: center;
  cursor: pointer;
}

.NavContainerJb_dtl {
  width: 78%;
  height: auto;
  position: absolute;
  left: 22%;
}
.navbar_Jb_dtl {
  height: 50px;
  width: 77.7%;
  position: fixed;
  left: 22.3%;
  z-index: 1;
  display: flex;
  box-shadow: 0 1px 4px grey;
  background-color: whitesmoke;
  align-items: center;
  justify-content: flex-end;
}
.list {
  display: flex;
}

.list li {
  margin: 0 30px 0 10px;
  list-style: none;
}
.list li a {
  text-decoration: none;
  color: #192a3e;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.list li a .fas,
.list li a .far {
  padding-right: 5px;
  color: #008bdf;
}

.whole {
  width: 100%;
  position: absolute;
  top: 52px;
}
.My_Js_My_As {
  display: flex;
  align-items: center;
  font-size: 1.2em;
  padding-bottom: 2px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.527);
}
.my_job {
  background-color: rgba(128, 128, 128, 0.527);
  color: grey;
  padding-left: 5px;
  display: flex;
  height: 70px;
  align-items: center;
}
.my_job h1,
.crte_jb h1 {
  font-size: 1.5em;
  font-weight: 100;
  cursor: pointer;
}
.arrow {
  width: 40px;
  height: 55px;
  font-weight: 100;
  line-height: 70px;
  background-color: #fff;
  border-left: 40px solid rgba(128, 128, 128, 0.527);
  border-top: 35px solid transparent;
  border-bottom: 35px solid transparent;
}
.crte_jb {
  margin-left: 20px;
  color: grey;
}

.Jb_dtl h2 {
  font-size: 30px;
  line-height: 40px;
  font-weight: 100;
  color: grey;
  padding: 13px 30px;
  border-bottom: 1px solid gray;
}

.Jb_dtl_p {
  width: 95%;
  font-size: 20px;
  padding: 10px 30px;
}
.Respns_inp_div {
  padding: 20px 30px;
}

.Respns_inputs .label {
  width: 200px;
  font-size: 20px;
  text-align: right;
}
.whole .fa-question-circle {
  color: rgb(22, 217, 243);
  margin-left: 5px;
  font-size: 20px;
  cursor: pointer;
}
.Respns_inputs {
  display: flex;
  margin-bottom: 40px;
}
.Respns_inputs .inpts {
  padding-bottom: 10px;
  display: flex;
  align-items: center;
}
.Respns_x,
.Rspns_d {
  padding: 2.3px 6px;
  border: 1px solid grey;
  border-left: none;
  color: rgb(41, 41, 41);
  background-color: rgba(128, 128, 128, 0.35);
  font-weight: bold;
  cursor: pointer;
  border-radius: 0 4px 4px 0;
}
.Rspns_dlr {
  padding: 2.3px 6px;
  border: 1px solid grey;
  border-right: none;
  color: rgb(41, 41, 41);
  background-color: rgba(128, 128, 128, 0.35);
  font-weight: bold;
  cursor: pointer;
  margin-left: 30px;
  border-radius: 4px 0 0 4px;
}
.Respns_inputs input {
  padding: 5px;
  margin-left: 30px;
  outline: none;
  border: 1px solid grey;
  width: 370px;
  font-size: 16px;
  border-radius: 4px 0 0 4px;
}
.ADD_others {
  margin: 5px 0 0 25px;
}

.ADD_others a {
  color: #0378c0;
  font-size: 15px;

  text-decoration: none;
}
.Respns_inputs .inpts :nth-child(3) {
  width: 110px;
  margin-left: 5px;
}
.Respns_inputs .slry_inpt {
  margin-left: 0;
  padding: 5px;
  width: 200px !important;
  outline: none;
  border: 1px solid grey;
  width: 370px;
  font-size: 16px;
  border-radius: 0 4px 4px 0;
}
.label .optnl {
  font-size: 15px;
  padding-right: 20px;
  font-style: italic;
}
.rdio_pr {
  margin: 5px 0 0 10px;
}
.rdio_pr #pr_yr,
.rdio_pr #pr_hr {
  width: auto;
  margin: 5px 5px 3px 10px;
}
.Respns_inputs .txtarea {
  margin-left: 30px;
}
.txtarea textarea {
  font-size: 15px;
  border: 1px solid grey;
  outline: none;
}
.kywrds .da_key_qstn {
  display: flex;
  align-items: center;
  padding-left: 20px;
}
.da_key_qstn :nth-child(1) {
  padding: 0 7px 0 4px;
  color: rgb(22, 217, 243);
}
.da_key_qstn label {
  line-height: 40px;
  font-weight: 100;
  color: grey;
  font-size: 27px;
}
.da_key_qstn :nth-child(3) {
  padding-top: 5px;
}
.kywrds .key_input {
  margin: 10px 0 40px 30px;
}
.kywrds .key_input input {
  width: 250px;
  padding: 6px;
  font-size: 16px;
  border-radius: 5px;
  outline: none;
  border: 1px solid grey;
}

